* {
    font-size: 100%;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}

/* Navbar */
#navbar {
    position: sticky;
    top: 0;
    overflow: hidden;

    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 72px;
    z-index: 1000;
    padding: 10px;
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.20);
    background: #dcedf5;
}


/* Mediatypes */
/* Handy */
@media (max-width: 768px) {
    .form.LoginForm img {
        width: 100px;
        height: 100px;
    }

    .header.MainHeader {
        font-size: 25px;
    }

    .mainContentDiv {
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 16px;
    }

    .mainContentDiv.loginForm {
        text-align: center;
        padding: 0 20px 20px 20px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 90vh;
    }

    #navbarHeaderText {
        font-size: 24px;
    }

    #navbarLogout {
        border: none;
        background: transparent;
        width: 60px;
        height: 60px;
        padding-right: 10%;
    }

    #navbarLogout img {
        width: 60px;
        height: 60px;
    }

    .errorContainer {
        position: absolute;
        top: 600px;
        color: rgb(228, 3, 3);
        padding: 15px;
    }

}

/* Desktop */
@media (min-width: 769px) {
    .form.LoginForm img {
        width: 150px;
        height: 150px;
    }

    .header.MainHeader {
        font-size: 40px;
    }

    .mainContentDiv.loginForm {
        text-align: center;
        padding: 0 20px 20px 20px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 90vh;
    }

    #navbarHeaderText {
        font-size: 27px;
    }

    #navbarLogout {
        border: none;
        background: transparent;
        width: 70px;
    }

    #navbarLogout img {
        width: 70px;
        height: 70px;
    }

    .errorContainer {
        position: absolute;
        top: 710px;
        color: rgb(228, 3, 3);
        padding: 15px;
    }
}

/* Components */
.card.MainContent {
    margin-top: 10%;
    background: #F0F0F0;
    display: flex;
    padding: 24px 24px 32px 24px;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    align-self: stretch;
    border-radius: 10px;
    border: none;
    align-items: center;
}

/* InputField */
.inputField {
    width: 100%;
    display: flex;
    padding: var(--ibk-spacing-10, 10px);
    align-items: center;
    gap: var(--ibk-spacing-8, 8px);
    align-self: stretch;
    border-radius: 10px;
    border: 2px solid var(#BDBDBD);
    background: var(--white-100, #FFF);
    border: 1px solid #BDBDBD;
    margin-top: -20px;
}

.customButton {
    border-radius: 10px;
    height: 40px;
    width: 150px;
    border: none;
    color: black;
}

.customButton.primary {
    background: #fdba12;
}

.customButton.secondary {
    background: #b8b8b8;
}


/* Button Container */
.buttonContainer {
    padding-left: 16px;
    padding-bottom: 16px;
}

/* Sticky Bottom Navbar */
.stickyNavbar {
    background: var(--white-100, #FFF);
    box-shadow: 0px -4px 15px 0px rgba(0, 0, 0, 0.15);
    width: 100%;
    position: fixed;
    bottom: 0;
    text-align: center;
    display: flex;
    justify-content: space-between;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Modal */
#customModal {
    min-height: 400px;
    border: none;
    max-width: 90%;
    margin: auto;
    margin-top: 50%;
    border-radius: var(--10, 10px);
}

.modalIconContainer.info,
.modalIconContainer.error {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    margin-top: 5%;
    margin-bottom: 5%;
    padding: var(--Spacing-12, 12px);
    gap: var(--Spacing-8, 8px);
    border-radius: var(--Spacing-48, 48px);
    width: 62px;
    height: 62px;
}

.modalContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 13%;
}

#modalButtonContainer button {
    min-width: 200px;
}